import {LoadingController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { ParameterColumnViewModel } from "../../Model/ViewModel/ParameterColumnViewModel";

/**
 * Generated class for the ParameterColumnModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-parameter-column-add-edit-modal",
  templateUrl: "parameter-column-add-edit-modal.html"
})
export class ParameterColumnAddEditModalPage {
  item: any;
  mode: string;
  CanEditBatch: boolean;
  title:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
     public loadingCtrl: LoadingController,    
  ) {

    this.item = new ParameterColumnViewModel();
    this.item.parameter_column_group=navParams.data.item.parameter_column_group;
    this.item.parameter_column=navParams.data.item.parameter_column;
    this.item.parameter_column_value=navParams.data.item.parameter_column_value;
    this.item.parameter_column_value_format=navParams.data.item.parameter_column_value_format;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    


    this.CanEditBatch = navParams.data.CanEditBatch;
    this.mode = navParams.data.mode;
    this.title="參數欄位";
  }
 

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ParameterColumnModalPage");
  }
}



