export class ParameterColumnViewModel {
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  參數欄位
  /// <summary>
  public parameter_column: string;
  /// <summary>
  ///  參數欄位內容
  /// <summary>
  public parameter_column_value: string;
  /// <summary>
  ///  參數欄位內容格式
  /// <summary>
  public parameter_column_value_format: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
}



